
@media only screen and (min-width: 1224px) {

    html{
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
    }
    
    header{
        width: 100%;
        height: 8.5%;
        background-color:rgb(2, 0, 54);
        display: flex;
        align-items: center;
        position: fixed;
    }
    
    header img{
        align-self: flex-start;
        padding: 10px;
    /*     width:max-content; */
        height: 80px;
        width: 190px;
        padding-left: 50px
    }
    
    .menu{
        width: 100%;
        display: inline-flex;
        list-style-type: none;
        justify-content: flex-end;
        padding-right: 50px;
      
    }
    
    .menu li{
        color: white;
        margin: 0 15px 0 15px;
    }
    
    a {
        float: left;
        display: block;
        color: #f2f2f2;
        text-align: center;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 17px;
        font-family: "Alata";
    
      }
      
      a:hover {
        background: #ffffff;
        color: rgb(25, 0, 114);
        border-radius: 10px;
      }
  }
  
  
*{
    margin: 0;
    padding: 0;
}

@media only screen and (max-width: 1224px) {

    
header{
    width: 100%;
    height: 8.5%;
    background-color:rgb(2, 0, 54);
    display: flex;
    align-items: center;
    position: fixed;
}

header img{
    align-self: flex-start;
    padding: 10px;
/*     width:max-content; */
    height: 25px;
    width: 80px;
    padding-left: 20px;
    padding-top: 16px;
}

.menu{
    width: 100%;
    display: inline-flex;
    list-style-type: none;
    justify-content: flex-end;
    padding-right: 50px;
}

.menu li{
    color: white;
    margin: 0 6px 0 10px;
    font-size: 10px;

}

a {

    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    text-decoration: none;
    font-size: 10px;
    font-family: "Alata";

  }
  
  a:hover {
    background: #ffffff;
    color: rgb(25, 0, 114);
    border-radius: 10px;
  }


 
}
