
@media only screen and (min-width: 1224px) {


  .enrollment-form{
      padding-top: 80px;
      margin:  auto;
      width: 70%;
  }
  .grid-container {
          display: inline-grid;
          grid-template-columns: auto auto auto;
          /* background-color: #2196F3; */
          padding: 5px;
          border-radius: 20px;
          column-gap: 30px;
          row-gap: 30px;
  
      }
    
  .top-text {
        color:rgb(2, 0, 54);
        font-family: "Alata";
        padding-top: 100px;
        padding-bottom: 30px;
        font-size: 30px;
        margin-right: 180px;
        }
    }
    

    

    
