.login {
    margin: 20px auto;
    width: 300px;
    }

.first-form{


    padding-top: 200px;
  
}

.login-screen {
    background-color:rgb(2, 0, 54);
    padding: 20px;
    border-radius: 5px;
    }

.app-title {
    text-align: center;
    color:rgb(2, 0, 54);
    font-family: "Alata";
    }
    
.login-form {
    text-align: center;
    }

.control-group {
    margin-bottom: 10px;
    }
    
input {
    text-align: center;
    background-color: #ECF0F1;
    border: 1px solid black;
    border-radius: 3px;
    font-size: 16px;
    font-weight: 200;
    padding: 10px 0;
    width: 250px;
    transition: border .5s;
    font-family: "Alata";

}


input:focus {
    border: 2px solid #3498DB;
    box-shadow: none;
    }

input:hover {
        border: 2px solid rgb(2, 0, 54);
        box-shadow: none;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

    }
    
    
.btn {
      border: 2px solid transparent;
      background: rgb(2, 0, 54);
      color: #ffffff;
      font-size: 16px;
      line-height: 25px;
      padding: 10px 0;
      text-decoration: none;
      text-shadow: none;
      border-radius: 3px;
      box-shadow: none;
      transition: 0.25s;
      display: block;
      width: 250px;
      margin: 0 auto;
      margin-top: 50px;
      font-family: "Alata";

    }
    
.btn:hover {
      background-color: #ffffff;
      color: #000;
      font-family: "Alata";

    }
    
.help-button{
      font-size: 12px;
      color: #000000;
      display: block;
      padding-top: 50px;
    }

    .help-button:hover{



        background-color: rgba(255, 255, 255, 0);
        color: #797777;
    }